@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&family=Roboto+Slab&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Original+Surfer&family=Rokkitt&display=swap);
*{box-sizing:border-box}body,.main-container,.main-container{width:100vw;height:100vh;margin:0}.app-name{border-radius:15px;padding:10px 20px;background:rgba(0,0,0,0.25);color:white}.app-name h1,.app-name h3{display:inline-block;padding:0px 10px;margin:0}.main-container{display:-webkit-flex;display:flex;position:relative;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;background:#0E040D;background:linear-gradient(to bottom, #0E040D 0%, #3789F6 41%, #B9893E 100%)}.app-container{display:block;position:relative;width:90vw;height:auto;max-width:520px;box-shadow:1px 1px 6px 0px rgba(0,0,0,0.81);border-radius:20px;background:rgba(162,198,246,0.225);padding:20px;font-family:'Fira Sans'}.app-container .top-display{display:-webkit-flex;display:flex;position:relative;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;width:100%;height:auto;min-height:100px}.app-container .top-display .top-left{display:block;position:relative;width:40%;height:auto;text-align:center}.app-container .top-display .top-right{display:-webkit-flex;display:flex;position:relative;width:40%;height:100%;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-content:space-between;align-content:space-between;text-align:center}.app-container .top-display .top-right #temp{font-size:60px;padding-bottom:10px;cursor:pointer}.app-container .top-display .top-right #temp:active{font-size:48px}.app-container .top-display img{width:60px;height:60px}.app-container .bottom-display{display:-webkit-flex;display:flex;position:relative;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100%;height:auto;min-height:100px;margin-top:15px}.app-container .bottom-display .change,.app-container .bottom-display .time-change{display:-webkit-flex;display:flex;position:relative;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100%;height:auto}.app-container .bottom-display .time-change div{display:block;position:relative;padding:8px}.app-container .bottom-display .change-end{display:-webkit-flex;display:flex;position:relative;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100%;height:auto}.app-container .bottom-display img{width:60px;height:60px}.reset{display:block;position:absolute;width:auto;height:auto;border-radius:50%;bottom:30px;right:30px;cursor:pointer}.reset img{width:24px;height:24px}.reset:active{-webkit-animation:rotateReset 0.5s alternate;animation:rotateReset 0.5s alternate;-webkit-transform:scale(0.9);transform:scale(0.9)}.footer{display:block;position:relative;width:auto;height:auto;font-weight:bold;background:rgba(255,255,255,0.25);padding:5px 10px;border-radius:10px}.footer a{text-decoration:none;color:#A51C12}@-webkit-keyframes rotateReset{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}}@keyframes rotateReset{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

